___CSS_LOADER_EXPORT___.push([module.id, "\n.scroll-wrapper {\n    position: fixed;\n    opacity: 0;\n    visibility: hidden;\n    overflow: hidden;\n    text-align: center;\n    font-size: 20px;\n    z-index: 999;\n    background-color: #777;\n    color: #eee;\n    width: 50px;\n    height: 48px;\n    line-height: 48px;\n    right: 30px;\n    bottom: 30px;\n    padding-top: 2px;\n    border-radius: 10px;\n    -webkit-transition: all 0.5s ease-in-out;\n    -moz-transition: all 0.5s ease-in-out;\n    -ms-transition: all 0.5s ease-in-out;\n    -o-transition: all 0.5s ease-in-out;\n    transition: all 0.5s ease-in-out;\n}\n.scroll-wrapper.show {\n    visibility: visible;\n    cursor: pointer;\n    opacity: 1;\n}\n.scroll-wrapper.left {\n    position: fixed;\n    right: 150px;\n}\n.scroll-wrapper.right {\n    position: fixed;\n    right: 90px;\n}\n", "",{"version":3,"sources":["webpack://./src/components/helpers/scroll-buttons.vue"],"names":[],"mappings":";AA8FA;IACA,eAAA;IACA,UAAA;IACA,kBAAA;IACA,gBAAA;IACA,kBAAA;IACA,eAAA;IACA,YAAA;IACA,sBAAA;IACA,WAAA;IACA,WAAA;IACA,YAAA;IACA,iBAAA;IACA,WAAA;IACA,YAAA;IACA,gBAAA;IACA,mBAAA;IACA,wCAAA;IACA,qCAAA;IACA,oCAAA;IACA,mCAAA;IACA,gCAAA;AACA;AAEA;IACA,mBAAA;IACA,eAAA;IACA,UAAA;AACA;AAEA;IACA,eAAA;IACA,YAAA;AACA;AAEA;IACA,eAAA;IACA,WAAA;AACA","sourcesContent":["<template>\n    <div id=\"scroll-buttons-wrapper\">\n        <div class=\"scroll-wrapper top\" :class=\"{ show: showToTop }\" @click.prevent=\"scrollTop\">\n            <span class=\"scroll-top-inner\">\n                <i class=\"glyphicon glyphicon-circle-arrow-up\" />\n            </span>\n        </div>\n\n        <div class=\"scroll-wrapper left\" :class=\"{ show: showLeftRight }\">\n            <span class=\"scroll-left-inner\">\n                <i @click.prevent=\"scrollLeft\" class=\"glyphicon glyphicon-circle-arrow-left\" />\n            </span>\n        </div>\n\n        <div class=\"scroll-wrapper right\" :class=\"{ show: showLeftRight }\">\n            <span class=\"scroll-right-inner\">\n                <i @click.prevent=\"scrollRight\" class=\"glyphicon glyphicon-circle-arrow-right\" />\n            </span>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    name: 'scroll-buttons',\n    data() {\n        return {\n            showToTop: false,\n            showLeftRight: false\n        };\n    },\n    methods: {\n        scrollTop() {\n            const { scrollTo } = this;\n            scrollTo($('body'));\n        },\n        scrollLeft() {\n            $('div.horizontal-scroll').animate({\n                scrollLeft: '-=153'\n            }, 1000, 'easeOutQuad');\n        },\n        scrollRight() {\n            $('div.horizontal-scroll').animate({\n                scrollLeft: '+=153'\n            }, 1000, 'easeOutQuad');\n        },\n        scrollTo(dest) {\n            $('html, body').animate({\n                scrollTop: $(dest).offset().top\n            }, 500, 'linear');\n        },\n        /**\n         * Make an attempt to detect if there are currently scroll bars visible for divs with the horizontal-scroll class.\n         *\n         * If scroll bars are visible the fixed left and right buttons become visible on that page.\n         */\n        initHorizontalScroll() {\n            const scrollDiv = $('div.horizontal-scroll').get();\n            if (scrollDiv.length === 0) {\n                return;\n            }\n\n            const scrollbarVisible = scrollDiv.map(el => {\n                return (el.scrollWidth > el.clientWidth);\n            }).indexOf(true);\n\n            if (scrollbarVisible >= 0) {\n                this.showLeftRight = true;\n            } else {\n                this.showLeftRight = false;\n            }\n        }\n    },\n    mounted() {\n        const { initHorizontalScroll } = this;\n\n        initHorizontalScroll();\n\n        $(window).on('resize', () => {\n            initHorizontalScroll();\n        });\n\n        $(document).on('scroll', () => {\n            if ($(window).scrollTop() > 100) {\n                this.showToTop = true;\n            } else {\n                this.showToTop = false;\n            }\n        });\n    }\n};\n</script>\n\n<style>\n.scroll-wrapper {\n    position: fixed;\n    opacity: 0;\n    visibility: hidden;\n    overflow: hidden;\n    text-align: center;\n    font-size: 20px;\n    z-index: 999;\n    background-color: #777;\n    color: #eee;\n    width: 50px;\n    height: 48px;\n    line-height: 48px;\n    right: 30px;\n    bottom: 30px;\n    padding-top: 2px;\n    border-radius: 10px;\n    -webkit-transition: all 0.5s ease-in-out;\n    -moz-transition: all 0.5s ease-in-out;\n    -ms-transition: all 0.5s ease-in-out;\n    -o-transition: all 0.5s ease-in-out;\n    transition: all 0.5s ease-in-out;\n}\n\n.scroll-wrapper.show {\n    visibility: visible;\n    cursor: pointer;\n    opacity: 1;\n}\n\n.scroll-wrapper.left {\n    position: fixed;\n    right: 150px;\n}\n\n.scroll-wrapper.right {\n    position: fixed;\n    right: 90px;\n}\n</style>\n"],"sourceRoot":""}]);
