 isChecked = event.currentTarget.checked;\n            // Add to list\n            if (isChecked && !selectedLists.includes(currentList)) {\n                selectedLists.push(currentList);\n            }\n\n            // Remove from list\n            const newList = selectedLists.filter(list => list !== currentList || (currentList && isChecked));\n            setTraktSelectedLists(newList);\n        }\n    }\n};\n</script>\n<style scoped>\nul {\n    padding-left: 0;\n}\n\nli {\n    list-style-type: none;\n}\n\n.trakt-list-group {\n    display: inline-flex;\n    height: 25px;\n}\n\ninput {\n    display: inline-block;\n    box-sizing: border-box;\n}\n\ninput.available-list {\n    height: 22px;\n}\n\ninput[type=checkbox] {\n    width: 20px;\n    height: 20px;\n}\n\n.max-width {\n    max-width: 450px;\n}\n</style>\n"],"sourceRoot":""}]);
