l thumb */\n}\n.recommended-image[data-v-e6209930] {\n    position: relative;\n    overflow: hidden;\n}\n.recommended-image[data-v-e6209930] .show-image {\n    height: 100%;\n}\n.show-image[data-v-e6209930] {\n    height: 100%;\n    border-top-left-radius: 5px;\n    border-top-right-radius: 5px;\n}\n.toggle-plot[data-v-e6209930] {\n    position: absolute;\n    right: 0;\n    top: 0;\n    z-index: 1;\n    text-decoration: underline;\n    cursor: pointer;\n    background-color: rgb(51, 51, 51);\n    padding: 0 2px;\n}\n.plot-enter-active[data-v-e6209930],\n.plot-leave-active[data-v-e6209930] {\n    -moz-transition-duration: 0.3s;\n    -webkit-transition-duration: 0.3s;\n    -o-transition-duration: 0.3s;\n    transition-duration: 0.3s;\n    -moz-transition-timing-function: ease-in;\n    -webkit-transition-timing-function: ease-in;\n    -o-transition-timing-function: ease-in;\n    transition-timing-function: ease-in;\n}\n.plot-enter-to[data-v-e6209930],\n.plot-leave[data-v-e6209930] {\n    bottom: 0;\n}\n.plot-enter[data-v-e6209930],\n.plot-leave-to[data-v-e6209930] {\n    bottom: -100%;\n}\n.add-show-progress[data-v-e6209930] {\n    width: 95px;\n    height: 21px;\n    position: relative;\n}\n.add-show-progress > .bar[data-v-e6209930] {\n    background-color: darkgrey;\n    height: 100%;\n    width: 0;\n    position: absolute;\n    top: 0;\n    opacity: 0.4;\n}\n.add-show-progress > .steps[data-v-e6209930] {\n    display: none;\n    position: absolute;\n    background-color: rgb(95, 95, 95);\n    font-size: 14px;\n    border: 1px solid rgb(125, 125, 125);\n    z-index: 1;\n    width: auto;\n    white-space: nowrap;\n}\n.add-show-progress:hover > .steps[data-v-e6209930] {\n    display: block;\n}\n.add-show-progress > .steps ul[data-v-e6209930] {\n    padding: 2px 5px;\n}\n.add-show-progress > .steps li[data-v-e6209930] {\n    list-style-type: none;\n    text-align: left;\n}\n", "",{"version":3,"sources":["webpack://./src/components/recommended-poster.vue"],"names":[],"mappings":";AA4OA;IACA,UAAA;IACA,kBAAA;IACA,QAAA;IACA,QAAA;IACA,4CAAA;IACA,yCAAA;IACA,wCAAA;IACA,uCAAA;IACA,oCAAA;AACA;AAEA;IACA,YAAA;AACA;AAEA;IACA,cAAA;IACA,YAAA;AACA;AAEA;IACA,iBAAA;AACA;AAEA;IACA,iBAAA;IACA,kBAAA;IACA,gBAAA;IACA,4BAAA;IACA,kBAAA;IACA,iCAAA;IACA,yBAAA;IACA,mFAAA;IACA,mCAAA;IACA,YAAA;IACA,gBAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,YAAA;IACA,WAAA;IACA,YAAA;IACA,kBAAA;IACA,SAAA;IACA,gBAAA;IACA,iCAAA;IACA,yCAAA;IACA,qBAAA;AACA;AAEA;IACA,gBAAA;IACA,YAAA;IACA,cAAA;IACA,eAAA;AACA;AAEA;IACA,UAAA,EAAA,kCAAA;AACA;AAEA;IACA,oBAAA,EAAA,+BAAA;AACA;AAEA;IACA,iCAAA,EAAA,8BAAA;IACA,kBAAA,EAAA,kCAAA;IACA,+BAAA,EAAA,wCAAA;AACA;AAEA;IACA,kBAAA;IACA,gBAAA;AACA;AAEA;IACA,YAAA;AACA;AAEA;IACA,YAAA;IACA,2BAAA;IACA,4BAAA;AACA;AAEA;IACA,kBAAA;IACA,QAAA;IACA,MAAA;IACA,UAAA;IACA,0BAAA;IACA,eAAA;IACA,iCAAA;IACA,cAAA;AACA;AAEA;;IAEA,8BAAA;IACA,iCAAA;IACA,4BAAA;IACA,yBAAA;IACA,wCAAA;IACA,2CAAA;IACA,sCAAA;IACA,mCAAA;AACA;AAEA;;IAEA,SAAA;AACA;AAEA;;IAEA,aAAA;AACA;AAEA;IACA,WAAA;IACA,YAAA;IACA,kBAAA;AACA;AAEA;IACA,0BAAA;IACA,YAAA;IACA,QAAA;IACA,kBAAA;IACA,MAAA;IACA,YAAA;AACA;AAEA;IACA,aAAA;IACA,kBAAA;IACA,iCAAA;IACA,eAAA;IACA,oCAAA;IACA,UAAA;IACA,WAAA;IACA,mBAAA;AACA;AAEA;IACA,cAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,qBAAA;IACA,gBAAA;AACA","sourcesContent":["<template>\n    <div class=\"poster-wrapper\">\n        <div class=\"recommended-image\">\n            <app-link :href=\"show.imageHref\">\n                <asset :default-src=\"show.imageSrc\" lazy type=\"posterThumb\" cls=\"show-image\" :link=\"false\" height=\"273px\" :img-width=\"186\" />\n            </app-link>\n\n            <div v-if=\"show.genres.length > 0\" class=\"tag-container\">\n                <ul class=\"genre-tags\">\n                    <li v-for=\"genre in show.genres\" :key=\"genre\">{{genre}}</li>\n                </ul>\n            </div>\n\n            <transition name=\"plot\">\n                <div v-if=\"displayPlot && show.plot\" class=\"plot-overlay\">\n                    <span>{{show.plot}}</span>\n                </div>\n            </transition>\n        </div>\n\n        <div class=\"check-overlay\" />\n\n        <div class=\"show-title\">\n            <span :title=\"show.title\">{{show.title}}</span>\n            <div v-if=\"show.plot\" class=\"toggle-plot\" @click=\"displayPlot = !displayPlot\">\n                <span v-if=\"displayPlot\">hide plot</span>\n                <span v-else>show plot</span>\n            </div>\n        </div>\n\n        <div class=\"row\">\n            <div name=\"left\" class=\"col-md-7 col-xs-12\">\n                <div class=\"show-rating\">\n                    <p>{{show.rating.toFixed(1)}} <img src=\"images/heart.png\">\n                        <template v-if=\"show.isAnime\" id=\"linkAnime\">\n                            <app-link class=\"recommended-show-url\" :href=\"`https://anidb.net/a${show.externals.aid}`\">\n                                <img src=\"images/anidb_inline_refl.png\" class=\"recommended-show-link-inline\" alt=\"\">\n                            </app-link>\n                        </template>\n                        <template v-if=\"show.recommender === 'Trakt Popular'\" id=\"linkAnime\">\n                            <a class=\"recommended-show-url\" :href=\"`https://trakt.tv/shows/${show.seriesId}`\">\n                                <img src=\"images/trakt.png\" class=\"recommended-show-link-inline\" alt=\"\">\n                            </a>\n                        </template>\n                    </p>\n                </div>\n\n                <div class=\"show-votes\">\n                    <i>x {{show.votes}}</i>\n                </div>\n\n            </div>\n\n            <div name=\"right\" class=\"col-md-5 col-xs-12\">\n                <div class=\"recommendedShowTitleIcons\">\n                    <button v-if=\"traktConfig.removedFromMedusa.includes(show.mappedSeriesId)\" class=\"btn-medusa btn-xs\">\n                        <app-link :href=\"`home/displayShow?indexername=${show.mappedIndexerName}&seriesid=${show.mappedSeriesId}`\">Watched</app-link>\n                    </button>\n                    <!-- if trakt_b and not (cur_show.show_in_list or cur_show.mapped_series_id in removed_from_medusa): -->\n                    <button :disabled=\"show.trakt.blacklisted\" v-if=\"show.source === externals.TRAKT\" :data-indexer-id=\"show.mappedSeriesId\" class=\"btn-medusa btn-xs\" @click=\"blacklistTrakt(show)\">Blacklist</button>\n                </div>\n            </div>\n        </div>\n        <div class=\"row\">\n            <div class=\"col-md-12 addshowoptions\">\n                <template v-if=\"show.showInLibrary\">\n                    <button v-if=\"show.showInLibrary\" class=\"btn-medusa btn-xs\">\n                        <div v-if=\"addingShow.started && addingShow.progress !== 100\" class=\"add-show-progress\" style=\"\">\n                            <span>Adding Show...</span>\n                            <div class=\"steps\">\n                                <ul>\n                                    <li v-for=\"step in addingShow.steps\" :key=\"step\">\n                                        <span>{{step}}</span>\n                                    </li>\n                                </ul>\n                            </div>\n                            <div class=\"bar\" :style=\"`width: ${addingShow.progress}%`\" />\n                        </div>\n                        <app-link v-else :href=\"`home/displayShow?showslug=${show.showInLibrary}`\">Open in library</app-link>\n                    </button>\n                </template>\n                <template v-else>\n                    <select :ref=\"`${show.source}-${show.seriesId}`\" v-model=\"selectedAddShowOption\" name=\"addshow\" class=\"rec-show-select\">\n                        <option v-for=\"option in addShowOptions(show)\" :value=\"option.value\" :key=\"option.value\">{{option.text}}</option>\n                    </select>\n                    <button :disabled=\"show.trakt.blacklisted\" class=\"btn-medusa btn-xs rec-show-button\" @click=\"addShow(show)\">\n                        Search/Add\n                    </button>\n                </template>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { mapState } from 'vuex';\nimport { api, apiRoute } from '../api';\nimport {\n    Asset,\n    AppLink\n} from './helpers';\n\nexport default {\n    name: 'recommended-poster',\n    components: {\n        Asset,\n        AppLink\n    },\n    props: {\n        show: {\n            type: Object,\n            required: true\n        }\n    },\n    data() {\n        return {\n            displayPlot: false,\n            selectedAddShowOption: 'search',\n            addingShow: {\n                identifier: null,\n                progress: 0,\n                started: false,\n                steps: []\n            }\n        };\n    },\n    computed: {\n        ...mapState({\n            traktConfig: state => state.recommended.trakt,\n            externals: state => state.recommended.externals,\n            queueitems: state => state.shows.queueitems\n        })\n    },\n    methods: {\n        addShow(show) {\n            const { selectedAddShowOption } = this;\n            if (selectedAddShowOption === 'search') {\n                // Route to the add-new-show.vue component, with the show's title.\n                this.$router.push({\n                    name: 'addNewShow',\n                    params: {\n                        providedInfo: {\n                            showName: show.title\n                        }\n                    }\n                });\n                return;\n            }\n\n            let showId = null;\n            if (Object.keys(show.externals).length !== 0 && show.externals[selectedAddShowOption + '_id']) {\n                showId = { [selectedAddShowOption]: show.externals[selectedAddShowOption + '_id'] };\n            }\n\n            if (this.addShowById(showId)) {\n                show.showInLibrary = `${selectedAddShowOption}${show.externals[selectedAddShowOption + '_id']}`;\n            }\n        },\n        /**\n         * Add by show id.\n         * @param {number} showId - Show id.\n         */\n        async addShowById(showId) {\n            const { enableShowOptions, selectedShowOptions } = this;\n\n            const options = {};\n\n            if (enableShowOptions) {\n                options.options = selectedShowOptions;\n            }\n\n            try {\n                const response = await api.post('series', { id: showId, options });\n                if (response && response.data && response.data.identifier) {\n                    this.addingShow.identifier = response.data.identifier;\n                    this.addingShow.started = true;\n                }\n\n                this.$snotify.success(\n                    'Adding new show to library',\n                    'In progress',\n                    { timeout: 20000 }\n                );\n                return true;\n            } catch (error) {\n                this.$snotify.error(\n                    'Error while trying to add new show',\n                    'Error'\n                );\n            }\n            return false;\n        },\n        blacklistTrakt(show) {\n            show.trakt.blacklisted = true;\n            apiRoute(`addShows/addShowToBlacklist?seriesid=${show.externals.tvdb_id}`);\n        },\n        addShowOptions(show) {\n            const { externals } = show;\n            if (show.isAnime) {\n                return [{ text: 'Tvdb', value: 'tvdb_id' }];\n            }\n\n            const options = [];\n            // Add through the add-new-show.vue component\n            options.push({ text: 'search show', value: 'search' });\n\n            for (const external in externals) {\n                if (['tvdb_id', 'tmdb_id', 'tvmaze_id'].includes(external)) {\n                    const externalName = external.split('_')[0];\n                    options.push({ text: externalName, value: externalName });\n                }\n            }\n\n            return options;\n        }\n    },\n    watch: {\n        queueitems(queueItem) {\n            const { addingShow } = this;\n            if (!addingShow.started || !addingShow.identifier) {\n                return;\n            }\n            const foundItem = queueItem.find(item => item.identifier === addingShow.identifier);\n            if (foundItem) {\n                this.addingShow.steps = foundItem.step;\n                this.addingShow.progress = 100 / 6 * this.addingShow.steps.length;\n                if (foundItem.success) {\n                    this.addingShow.progress = 100;\n                }\n            }\n        }\n    }\n};\n</script>\n\n<style scoped>\n.tag-container {\n    opacity: 0;\n    position: absolute;\n    top: 3px;\n    right: 0;\n    -webkit-transition: opacity 0.2s ease-in-out;\n    -moz-transition: opacity 0.2s ease-in-out;\n    -ms-transition: opacity 0.2s ease-in-out;\n    -o-transition: opacity 0.2s ease-in-out;\n    transition: opacity 0.2s ease-in-out;\n}\n\n.recommended-image:hover {\n    opacity: 0.9;\n}\n\n.recommended-image:hover .tag-container {\n    display: block;\n    opacity: 0.9;\n}\n\nul.genre-tags {\n    margin-right: 2px;\n}\n\nul.genre-tags > li {\n    margin-right: 1px;\n    margin-bottom: 2px;\n    padding: 2px 4px;\n    background: rgb(21, 82, 143);\n    border-radius: 1px;\n    border: 1px solid rgb(17, 17, 17);\n    color: rgb(255, 255, 255);\n    font: 14px/18px \"Open Sans\", \"Helvetica Neue\", Helvetica, Arial, Geneva, sans-serif;\n    text-shadow: 0 1px rgb(0 0 0 / 80%);\n    float: right;\n    list-style: none;\n}\n\nselect.max-width {\n    max-width: 430px;\n}\n\n.plot-overlay {\n    opacity: 0.9;\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    bottom: 0;\n    overflow-y: auto;\n    background-color: rgb(51, 51, 51);\n    scrollbar-color: rgb(65, 0, 181) darkgrey;\n    scrollbar-width: thin;\n}\n\n.plot-overlay > span {\n    padding: 2px 5px;\n    height: 100%;\n    display: block;\n    font-size: 11px;\n}\n\n.plot-overlay::-webkit-scrollbar {\n    width: 6px; /* width of the entire scrollbar */\n}\n\n.plot-overlay::-webkit-scrollbar-track {\n    background: darkgrey; /* color of the tracking area */\n}\n\n.plot-overlay::-webkit-scrollbar-thumb {\n    background-color: rgb(65, 0, 181); /* color of the scroll thumb */\n    border-radius: 1px; /* roundness of the scroll thumb */\n    border: 0 solid rgb(5, 36, 249); /* creates padding around scroll thumb */\n}\n\n.recommended-image {\n    position: relative;\n    overflow: hidden;\n}\n\n.recommended-image >>> .show-image {\n    height: 100%;\n}\n\n.show-image {\n    height: 100%;\n    border-top-left-radius: 5px;\n    border-top-right-radius: 5px;\n}\n\n.toggle-plot {\n    position: absolute;\n    right: 0;\n    top: 0;\n    z-index: 1;\n    text-decoration: underline;\n    cursor: pointer;\n    background-color: rgb(51, 51, 51);\n    padding: 0 2px;\n}\n\n.plot-enter-active,\n.plot-leave-active {\n    -moz-transition-duration: 0.3s;\n    -webkit-transition-duration: 0.3s;\n    -o-transition-duration: 0.3s;\n    transition-duration: 0.3s;\n    -moz-transition-timing-function: ease-in;\n    -webkit-transition-timing-function: ease-in;\n    -o-transition-timing-function: ease-in;\n    transition-timing-function: ease-in;\n}\n\n.plot-enter-to,\n.plot-leave {\n    bottom: 0;\n}\n\n.plot-enter,\n.plot-leave-to {\n    bottom: -100%;\n}\n\n.add-show-progress {\n    width: 95px;\n    height: 21px;\n    position: relative;\n}\n\n.add-show-progress > .bar {\n    background-color: darkgrey;\n    height: 100%;\n    width: 0;\n    position: absolute;\n    top: 0;\n    opacity: 0.4;\n}\n\n.add-show-progress > .steps {\n    display: none;\n    position: absolute;\n    background-color: rgb(95, 95, 95);\n    font-size: 14px;\n    border: 1px solid rgb(125, 125, 125);\n    z-index: 1;\n    width: auto;\n    white-space: nowrap;\n}\n\n.add-show-progress:hover > .steps {\n    display: block;\n}\n\n.add-show-progress > .steps ul {\n    padding: 2px 5px;\n}\n\n.add-show-progress > .steps li {\n    list-style-type: none;\n    text-align: left;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
module.exports = ___CSS_LOADER_EXPORT___;
