\">\n                    <label :for=\"id\" class=\"control-label\">\n                        <span>{{ label }}</span>\n                    </label>\n                </div>\n                <div class=\"col-sm-10 content\">\n                    <div class=\"parent\" :class=\"inputClass\">\n                        <input v-bind=\"{id, type, name: id, placeholder, disabled}\" v-model=\"localValue\" @input=\"updateValue()\">\n                        <transition name=\"uri-error\">\n                            <div v-if=\"uriError\" class=\"uri-error\">Make sure to start your URI with http://, https://, scgi://, etc..</div>\n                        </transition>\n                        <slot name=\"warning\" />\n                    </div>\n                    <p v-for=\"(explanation, index) in explanations\" :key=\"index\">{{ explanation }}</p>\n                    <slot />\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    name: 'config-textbox',\n    props: {\n        label: {\n            type: String,\n            required: true\n        },\n        id: {\n            type: String,\n            required: true\n        },\n        explanations: {\n            type: Array,\n            default: () => []\n        },\n        value: {\n            type: String,\n            default: ''\n        },\n        type: {\n            type: String,\n            default: 'text'\n        },\n        disabled: Boolean,\n        /**\n         * Overwrite the default configured class on the <input/> element.\n         */\n        inputClass: {\n            type: String,\n            default: 'form-control input-sm max-input350'\n        },\n        placeholder: {\n            type: String,\n            default: ''\n        },\n        validateUri: Boolean\n    },\n    data() {\n        return {\n            localValue: null\n        };\n    },\n    mounted() {\n        const { value } = this;\n        this.localValue = value;\n    },\n    computed: {\n        uriError() {\n            const { localValue, validateUri } = this;\n            if (!validateUri || !localValue) {\n                return false;\n            }\n            return !localValue.match(/^[A-Za-z]{3,5}:\\/\\//);\n        }\n    },\n    watch: {\n        value() {\n            const { value } = this;\n            this.localValue = value;\n        }\n    },\n    methods: {\n        updateValue() {\n            const { localValue } = this;\n            this.$emit('input', localValue);\n        }\n    }\n};\n</script>\n\n<style scoped>\n.input75 {\n    width: 75px;\n    margin-top: -4px;\n}\n\n.input250 {\n    width: 250px;\n    margin-top: -4px;\n}\n\n.input350 {\n    width: 350px;\n    margin-top: -4px;\n}\n\n.input450 {\n    width: 450px;\n    margin-top: -4px;\n}\n\ninput {\n    margin-bottom: 5px;\n    width: 100%;\n    border: none;\n}\n\n.uri-error-enter-active,\n.uri-error-leave-active {\n    -moz-transition-duration: 0.3s;\n    -webkit-transition-duration: 0.3s;\n    -o-transition-duration: 0.3s;\n    transition-duration: 0.3s;\n    -moz-transition-timing-function: ease-in;\n    -webkit-transition-timing-function: ease-in;\n    -o-transition-timing-function: ease-in;\n    transition-timing-function: ease-in;\n}\n\n.uri-error-enter-to,\n.uri-error-leave {\n    max-height: 100%;\n}\n\n.uri-error-enter,\n.uri-error-leave-to {\n    max-height: 0;\n}\n\n.parent {\n    position: relative;\n}\n\ndiv.uri-error {\n    display: block;\n    overflow: hidden;\n    width: 100%;\n    position: absolute;\n    left: 0;\n    background-color: #e23636;\n    padding: 0 2px 0 2px;\n}\n</style>\n"],"sourceRoot":""}]);
