___CSS_LOADER_EXPORT___.push([module.id, "\n.input75 {\n    width: 75px;\n    margin-top: -4px;\n}\n.input250 {\n    width: 250px;\n    margin-top: -4px;\n}\n.input350 {\n    width: 350px;\n    margin-top: -4px;\n}\n.input450 {\n    width: 450px;\n    margin-top: -4px;\n}\n", "",{"version":3,"sources":["webpack://./src/components/helpers/config-toggle-slider.vue"],"names":[],"mappings":";AAkFA;IACA,WAAA;IACA,gBAAA;AACA;AAEA;IACA,YAAA;IACA,gBAAA;AACA;AAEA;IACA,YAAA;IACA,gBAAA;AACA;AAEA;IACA,YAAA;IACA,gBAAA;AACA","sourcesContent":["<template>\n    <div v-if=\"!experimental || experimentalEnabled\" id=\"config-toggle-slider-content\">\n        <div class=\"form-group\">\n            <div class=\"row\">\n                <div class=\"col-sm-2\">\n                    <label :for=\"id\" class=\"control-label\">\n                        <span>{{ label }}</span>\n                    </label>\n                </div>\n                <div class=\"col-sm-10 content\">\n                    <toggle-button :width=\"45\" :height=\"22\" v-bind=\"{id, name: id, disabled}\" v-model=\"localChecked\" sync @input=\"updateValue()\" />\n                    <p v-for=\"(explanation, index) in explanations\" :key=\"index\">{{ explanation }}</p>\n                    <slot />\n                    <span style=\"color: red\" v-if=\"experimental\">This is an experimental feature</span>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { ToggleButton } from 'vue-js-toggle-button';\nimport { mapState } from 'vuex';\n\nexport default {\n    name: 'config-toggle-slider',\n    components: {\n        ToggleButton\n    },\n    props: {\n        label: {\n            type: String,\n            required: true\n        },\n        id: {\n            type: String,\n            required: true\n        },\n        value: {\n            type: Boolean,\n            default: null\n        },\n        disabled: {\n            type: Boolean,\n            default: false\n        },\n        explanations: {\n            type: Array,\n            default: () => []\n        },\n        experimental: Boolean\n    },\n    data() {\n        return {\n            localChecked: null\n        };\n    },\n    mounted() {\n        const { value } = this;\n        this.localChecked = value;\n    },\n    computed: {\n        ...mapState({\n            experimentalEnabled: state => state.config.general.experimental\n        })\n    },\n    watch: {\n        value() {\n            const { value } = this;\n            this.localChecked = value;\n        }\n    },\n    methods: {\n        updateValue() {\n            const { localChecked } = this;\n            this.$emit('input', localChecked);\n        }\n    }\n};\n</script>\n\n<style>\n.input75 {\n    width: 75px;\n    margin-top: -4px;\n}\n\n.input250 {\n    width: 250px;\n    margin-top: -4px;\n}\n\n.input350 {\n    width: 350px;\n    margin-top: -4px;\n}\n\n.input450 {\n    width: 450px;\n    margin-top: -4px;\n}\n</style>\n"],"sourceRoot":""}]);
