___CSS_LOADER_EXPORT___.push([module.id, "\ndiv.select-list ul[data-v-48a6e4c2] {\n    padding-left: 0;\n}\ndiv.select-list li[data-v-48a6e4c2] {\n    list-style-type: none;\n    display: flex;\n}\ndiv.select-list .new-item[data-v-48a6e4c2] {\n    display: flex;\n}\ndiv.select-list .new-item-help[data-v-48a6e4c2] {\n    font-weight: bold;\n    padding-top: 5px;\n}\ndiv.select-list input[data-v-48a6e4c2],\ndiv.select-list img[data-v-48a6e4c2] {\n    display: inline-block;\n    box-sizing: border-box;\n}\ndiv.select-list.max-width[data-v-48a6e4c2] {\n    max-width: 450px;\n}\ndiv.select-list .switch-input[data-v-48a6e4c2] {\n    left: -8px;\n    top: 4px;\n    position: absolute;\n    z-index: 10;\n    opacity: 0.6;\n}\n.draggable-list[data-v-48a6e4c2] {\n    list-style: none;\n}\n.draggable-input-group[data-v-48a6e4c2] {\n    display: inline-flex;\n    width: 300px;\n}\n", "",{"version":3,"sources":["webpack://./src/components/helpers/sorted-select-list.vue"],"names":[],"mappings":";AA8IA;IACA,eAAA;AACA;AAEA;IACA,qBAAA;IACA,aAAA;AACA;AAEA;IACA,aAAA;AACA;AAEA;IACA,iBAAA;IACA,gBAAA;AACA;AAEA;;IAEA,qBAAA;IACA,sBAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,UAAA;IACA,QAAA;IACA,kBAAA;IACA,WAAA;IACA,YAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,oBAAA;IACA,YAAA;AACA","sourcesContent":["<template>\n    <div class=\"sorted-select-list max-width\" v-bind=\"{disabled}\">\n        <draggable\n            tag=\"ul\"\n            v-model=\"editItems\"\n            class=\"list-group\"\n            handle=\".handle\"\n            @start=\"dragging = true\"\n            @end=\"dragging = false\"\n        >\n            <li v-for=\"item of editItems\" :key=\"item.id\" class=\"draggable-list\">\n                <font-awesome-icon :icon=\"'align-justify'\" class=\"handle\" />\n                <div class=\"draggable-input-group\">\n                    <input class=\"form-control input-sm\" type=\"text\" v-model=\"item.value\" @input=\"removeEmpty(item)\">\n                    <div class=\"input-group-btn\" @click=\"deleteItem(item)\">\n                        <div style=\"font-size: 14px\" class=\"btn btn-default input-sm\">\n                            <i class=\"glyphicon glyphicon-remove\" title=\"Remove\" />\n                        </div>\n                    </div>\n                </div>\n            </li>\n        </draggable>\n        <div class=\"new-item\">\n            <div class=\"draggable-input-group\">\n                <input class=\"form-control input-sm\" type=\"text\" ref=\"newItemInput\" v-model=\"newItem\" placeholder=\"add new values per line\">\n                <div class=\"input-group-btn\" @click=\"addNewItem()\">\n                    <div style=\"font-size: 14px\" class=\"btn btn-default input-sm\">\n                        <i class=\"glyphicon glyphicon-plus\" title=\"Add\" />\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div v-if=\"newItem.length > 0\" class=\"new-item-help\">\n            Click <i class=\"glyphicon glyphicon-plus\" /> to finish adding the value.\n        </div>\n    </div>\n</template>\n<script>\nimport Draggable from 'vuedraggable';\nimport { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';\n\nexport default {\n    name: 'sorted-select-list',\n    components: { Draggable, FontAwesomeIcon },\n    props: {\n        listItems: {\n            type: Array,\n            default: () => [],\n            required: true\n        },\n        unique: {\n            type: Boolean,\n            default: true\n        },\n        disabled: Boolean,\n        sorted: Boolean\n    },\n    data() {\n        return {\n            editItems: [],\n            newItem: '',\n            indexCounter: 0,\n            dragging: false\n        };\n    },\n    mounted() {\n        this.editItems = this.sanitize(this.listItems);\n    },\n    created() {\n        /**\n         * ListItems property might receive values originating from the API,\n         * that are sometimes not available when rendering.\n         * @TODO: This is not ideal! Maybe we can remove this in the future.\n         */\n        const unwatchProp = this.$watch('listItems', () => {\n            unwatchProp();\n            this.editItems = this.sanitize(this.listItems);\n        });\n    },\n    methods: {\n        addItem(item) {\n            if (this.unique && this.editItems.find(i => i.value === item)) {\n                return;\n            }\n            this.editItems.push({ id: this.indexCounter, value: item });\n            this.indexCounter += 1;\n        },\n        addNewItem() {\n            const { newItem, editItems } = this;\n            if (this.newItem === '') {\n                return;\n            }\n            this.addItem(newItem);\n            this.newItem = '';\n            this.$emit('change', editItems);\n        },\n        deleteItem(item) {\n            this.editItems = this.editItems.filter(e => e !== item);\n            this.$refs.newItemInput.focus();\n            this.$emit('change', this.editItems);\n        },\n        removeEmpty(item) {\n            return item.value === '' ? this.deleteItem(item) : false;\n        },\n        /**\n         * Initially an array of strings is passed, which we'd like to translate to an array of object.\n         * Where the index has been added.\n         * @param {string[]} values - Array of strings.\n         * @returns {Object[]} - An array of objects with the index and value.\n         */\n        sanitize(values) {\n            if (!values) {\n                return [];\n            }\n\n            return values.map(value => {\n                if (typeof (value) === 'string') {\n                    this.indexCounter += 1;\n                    return {\n                        id: this.indexCounter - 1,\n                        value\n                    };\n                }\n                return value;\n            });\n        }\n    },\n    watch: {\n        listItems() {\n            this.editItems = this.sanitize(this.listItems);\n            this.newItem = '';\n        },\n        dragging(value) {\n            const { editItems } = this;\n            if (!value) {\n                this.$emit('change', editItems);\n            }\n        }\n    }\n};\n</script>\n<style scoped>\ndiv.select-list ul {\n    padding-left: 0;\n}\n\ndiv.select-list li {\n    list-style-type: none;\n    display: flex;\n}\n\ndiv.select-list .new-item {\n    display: flex;\n}\n\ndiv.select-list .new-item-help {\n    font-weight: bold;\n    padding-top: 5px;\n}\n\ndiv.select-list input,\ndiv.select-list img {\n    display: inline-block;\n    box-sizing: border-box;\n}\n\ndiv.select-list.max-width {\n    max-width: 450px;\n}\n\ndiv.select-list .switch-input {\n    left: -8px;\n    top: 4px;\n    position: absolute;\n    z-index: 10;\n    opacity: 0.6;\n}\n\n.draggable-list {\n    list-style: none;\n}\n\n.draggable-input-group {\n    display: inline-flex;\n    width: 300px;\n}\n</style>\n"],"sourceRoot":""}]);
