  background-position-y: bottom;\n        }\n\n        th.sortable button {\n            -webkit-appearance: none;\n            -moz-appearance: none;\n            appearance: none;\n            background: transparent;\n            border: none;\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n        }\n\n        thead th {\n            padding: 4px;\n            cursor: default;\n        }\n\n        input.tablesorter-filter {\n            width: 98%;\n            height: auto;\n            -webkit-box-sizing: border-box;\n            -moz-box-sizing: border-box;\n            box-sizing: border-box;\n        }\n\n        tr.tablesorter-filter-row,\n        tr.tablesorter-filter-row td {\n            text-align: center;\n        }\n\n        /* optional disabled input styling */\n        input.tablesorter-filter-row .disabled {\n            display: none;\n        }\n\n        tfoot tr {\n            text-align: center;\n            border-collapse: collapse;\n        }\n\n        tfoot a {\n            text-decoration: none;\n        }\n\n        th.vgt-row-header {\n            text-align: left;\n        }\n\n        .season-header {\n            display: inline;\n            margin-left: 5px;\n        }\n\n        tr.spacer {\n            height: 25px;\n        }\n\n        tr:hover {\n            opacity: 0.9;\n        }\n\n        /* When using collored rows (using the episode status name, Snatched, Downloaded, Failed, etc.)\n        * we'd like to have the text always black.\n        */\n        tr.status td > span {\n            color: rgb(0, 0, 0);\n        }\n\n        span.episode-title > a {\n            text-decoration: none;\n            \n            &:hover,\n            &:focus {\n                text-decoration: underline;\n            }\n        }\n\n        .skipped {\n            background-color: rgb(190, 222, 237);\n        }\n    \n        .snatched {\n            background-color: rgb(235, 193, 234);\n        }\n    \n        .downloaded {\n            background-color: rgb(255, 218, 138);\n        }\n    \n        .failed {\n            background-color: rgb(255, 153, 153);\n        }\n    \n        .subtitled {\n            background-color: rgb(190, 222, 237);\n        }\n\n        .archived {\n            background-color: rgb(245, 241, 228);\n        }\n    }    \n\n    .tablesorter-header-inner {\n        padding: 0 2px;\n        text-align: center;\n    }\n\n    .vgt-dropdown > .button-group {\n        position: relative;\n    }\n\n    .dropdown-toggle {\n        position: absolute;\n        z-index: 1;\n        top: 0.1em;\n        right: 0.1em;\n        width: 1em;\n        transition: width 0.2s ease-in-out;\n\n        &:hover,\n        &:active {\n            width: 2em;\n        }\n    }\n\n    .vgt-dropdown-menu {\n        position: absolute;\n        z-index: 1;\n        float: left;\n        min-width: 160px;\n        padding: 5px 0;\n        margin: 2px 0 0;\n        font-size: 14px;\n        text-align: left;\n        list-style: none;\n        background-clip: padding-box;\n        border-radius: 3px;\n        right: 0;\n        top: 2em;\n\n        > li > span {\n            display: block;\n            padding: 3px 5px;\n            clear: both;\n            font-weight: 400;\n            line-height: 1.42857143;\n            white-space: nowrap;\n        }\n    \n    }\n\n    .align-center {\n        display: flex;\n        justify-content: center;\n    }\n\n    .span-center > span {\n        display: flex;\n        justify-content: center;\n    }\n\n    .indexer-image :not(:last-child) {\n        margin-right: 5px;\n    }\n\n    .button-row {\n        width: 100%;\n        display: inline-block;\n    }\n\n    .global-ignored td.release span {\n        color: red;\n    }\n\n    .show-ignored td.release span {\n        color: red;\n        font-style: italic;\n    }\n\n    .global-required td.release span {\n        color: green;\n    }\n\n    .show-required td.release span {\n        color: green;\n        font-style: italic;\n    }\n\n    .global-undesired td.release span {\n        color: orange;\n    }\n\n    td.col-footer {\n        text-align: left !important;\n    }\n\n    .vgt-wrap__footer {\n        color: rgb(255, 255, 255);\n        padding: 1em;\n        background-color: rgb(51, 51, 51);\n        margin-bottom: 1em;\n        display: flex;\n        justify-content: space-between;\n    }\n\n    .footer__row-count,\n    .footer__navigation__page-info {\n        display: inline;\n    }\n\n    .footer__row-count__label {\n        margin-right: 1em;\n    }\n\n    .vgt-wrap__footer .footer__navigation {\n        font-size: 14px;\n    }\n\n    .vgt-pull-right {\n        float: right !important;\n    }\n\n    .footer__navigation__page-btn {\n        display: inline-block;\n        padding: 4px 10px;\n        margin-bottom: 0;\n        font-size: 12px;\n        line-height: 16px;\n        vertical-align: middle;\n        border-radius: 1px;\n    }\n\n    .footer__navigation__page-btn:hover {\n        text-decoration: none;\n        background-position: 0 -150px;\n        transition: background-position 0s linear;\n        background-image: none;\n    }\n\n    .footer__navigation__page-btn.disabled {\n        display: none;\n    }\n\n    .vgt-wrap__footer .footer__navigation__page-btn .chevron {\n        width: 24px;\n        height: 24px;\n        border-radius: 15%;\n        position: relative;\n        margin: 0 8px;\n    }\n\n    .vgt-wrap__footer .footer__navigation__info,\n    .vgt-wrap__footer .footer__navigation__page-info {\n        display: inline-flex;\n        color: #909399;\n        margin: 0 16px;\n        margin-top: 0;\n        margin-right: 16px;\n        margin-bottom: 0;\n        margin-left: 16px;\n    }\n\n    .vgt-input {\n        height: 23px;\n        line-height: 23px;\n        font-size: 0.9em;\n        width: 100%;\n        background-color: #fff;\n        background-image: none;\n        border: 1px solid #ccc;\n        border-radius: 3px;\n        padding: 0 10px;\n        margin: 0;\n    }\n    \n    .vgt-select {\n        height: 23px;\n        line-height: 23px;\n        font-size: 0.9em;\n        width: 100%;\n        background-color: #fff;\n        background-image: none;\n        border: 1px solid #ccc;\n        border-radius: 3px;\n        padding: 0 10px;\n    }\n\n    /* Schedule list layout */\n    .schedule tr.today {\n        background-color: rgb(245, 241, 228);\n    }\n\n    .schedule tr.soon {\n        background-color: rgb(221, 255, 221);\n    }\n\n    .schedule tr.missed {\n        background-color: rgb(255, 221, 221);\n    }\n\n    .schedule tr.later {\n        background-color: rgb(190, 222, 237);\n    }\n\n    .schedule tr,\n    .schedule tr a {\n        color: rgb(0, 0, 0);\n    }\n}","// variables\n$tooltipColor: #ffef93;\n$tooltipBorderColor: #f1d031;\n$invalidColor: #ff5b5b;\n\n.tooltip-wrapper {\n    float: left;\n    min-width: 340px;\n}\n\n.invalid {\n    background-color: $invalidColor;\n}\n\n.tooltip {\n    display: block !important;\n    z-index: 10000;\n\n    .tooltip-inner {\n        background: $tooltipColor;\n        color: #555;\n        border-radius: 16px;\n        padding: 5px 10px 4px;\n        border: 1px solid $tooltipBorderColor;\n        -webkit-box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n        -moz-box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n        box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n    }\n\n    .tooltip-arrow {\n        width: 0;\n        height: 0;\n        position: absolute;\n        margin: 5px;\n        border: 1px solid $tooltipColor;\n        z-index: 1;\n    }\n\n}\n\n.tooltip[x-placement^='top'] {\n    margin-bottom: 5px;\n    .tooltip-arrow {\n        border-width: 5px 5px 0 5px;\n        border-left-color: transparent !important;\n        border-right-color: transparent !important;\n        border-bottom-color: transparent !important;\n        bottom: -5px;\n        left: calc(50% - 4px);\n        margin-top: 0;\n        margin-bottom: 0;\n    }\n}\n\n.tooltip[x-placement^='bottom'] {\n    margin-top: 5px;\n    .tooltip-arrow {\n        border-width: 0 5px 5px 5px;\n        border-left-color: transparent !important;\n        border-right-color: transparent !important;\n        border-top-color: transparent !important;\n        top: -5px;\n        left: calc(50% - 4px);\n        margin-top: 0;\n        margin-bottom: 0;\n    }\n}\n\n.tooltip[x-placement^='right'] {\n    margin-left: 5px;\n    .tooltip-arrow {\n        border-width: 5px 5px 5px 0;\n        border-left-color: transparent !important;\n        border-top-color: transparent !important;\n        border-bottom-color: transparent !important;\n        left: -4px;\n        top: calc(50% - 5px);\n        margin-left: 0;\n        margin-right: 0;\n    }\n}\n\n.tooltip[x-placement^='left'] {\n    margin-right: 5px;\n    .tooltip-arrow {\n        border-width: 5px 0 5px 5px;\n        border-top-color: transparent !important;\n        border-right-color: transparent !important;\n        border-bottom-color: transparent !important;\n        right: -4px;\n        top: calc(50% - 5px);\n        margin-left: 0;\n        margin-right: 0;\n    }\n}\n\n.tooltip.popover {\n    .popover-inner {\n        background: $tooltipColor;\n        color: #555;\n        padding: 24px;\n        border-radius: 5px;\n        box-shadow: 0 5px 30px rgba(black, 0.1);\n    }\n\n    .popover-arrow {\n        border-color: $tooltipColor;\n    }\n}\n\n.tooltip[aria-hidden='true'] {\n    visibility: hidden;\n    opacity: 0;\n    transition: opacity 0.15s, visibility 0.15s;\n}\n\n.tooltip[aria-hidden='false'] {\n    visibility: visible;\n    opacity: 1;\n    transition: opacity 0.15s;\n}","/** Style the modal. This should be saved somewhere, where we create one modal template with slots, and style that. */\n.modal-container {\n    border: 1px solid rgb(17, 17, 17);\n    box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.175);\n    border-radius: 0;\n}\n\n.modal-header {\n    padding: 9px 15px;\n    border-bottom: none;\n    border-radius: 0;\n    background-color: rgb(55, 55, 55);\n}\n\n.modal-content {\n    background: rgb(34, 34, 34);\n    border-radius: 0;\n    border: 1px solid rgba(0, 0, 0, 0.2);\n    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);\n    color: white;\n}\n\n.modal-body {\n    background: rgb(34, 34, 34);\n    overflow-y: auto;\n}\n\n.modal-footer {\n    border-top: none;\n    text-align: center;\n}\n",".vue-tags-input {\n    width: 273px;\n    border: 1px solid rgb(204, 204, 204);\n    border-radius: 3px;\n\n    .ti-tag {\n        display: inline-flex;\n        width: 100%;\n    }\n\n    .ti-input {\n        border: none;\n    }\n\n    .country-left {\n        margin-right: 5px;\n    }\n}","svg.back-arrow {\n    color: #337ab7;\n    width: 20px;\n    height: 20px;\n    float: left;\n    margin-right: 1em;\n    cursor: pointer;\n}\n\nsvg.back-arrow:hover,\nsvg.back-arrow:focus {\n    color: #23527c;\n    transform: translateX(-2px);\n    transition: transform ease-in-out 0.2s;\n}"],"sourceRoot":""}]);
