var ___CSS_LOADER_API_IMPORT___ = require("../../node_modules/css-loader/dist/runtime/api.js");
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, "\n.floating-badge {\n    position: absolute;\n    top: -5px;\n    right: -8px;\n    padding: 0 4px;\n    background-color: #777;\n    border: 2px solid #959595;\n    border-radius: 100px;\n    font-size: 12px;\n    font-weight: bold;\n    text-decoration: none;\n    color: white;\n}\n", "",{"version":3,"sources":["webpack://./src/components/app-header.vue"],"names":[],"mappings":";AAoTA;IACA,kBAAA;IACA,SAAA;IACA,WAAA;IACA,cAAA;IACA,sBAAA;IACA,yBAAA;IACA,oBAAA;IACA,eAAA;IACA,iBAAA;IACA,qBAAA;IACA,YAAA;AACA","sourcesContent":["<template>\n    <nav class=\"navbar navbar-default navbar-fixed-top hidden-print\" role=\"navigation\">\n        <div class=\"container-fluid\">\n            <div class=\"navbar-header\">\n                <button type=\"button\" class=\"navbar-toggle collapsed\" data-toggle=\"collapse\" data-target=\"#main_nav\">\n                    <span v-if=\"toolsBadgeCount > 0\" :class=\"`floating-badge${toolsBadgeClass}`\">{{ toolsBadgeCount }}</span>\n                    <span class=\"sr-only\">Toggle navigation</span>\n                    <span class=\"icon-bar\" />\n                    <span class=\"icon-bar\" />\n                    <span class=\"icon-bar\" />\n                </button>\n                <app-link class=\"navbar-brand\" href=\"home/\" title=\"Medusa\"><img alt=\"Medusa\" src=\"images/medusa.png\" style=\"height: 50px;\" class=\"img-responsive pull-left\"></app-link>\n            </div>\n            <div v-if=\"isAuthenticated\" class=\"collapse navbar-collapse\" id=\"main_nav\">\n                <ul class=\"nav navbar-nav navbar-right\">\n                    <li id=\"NAVhome\" class=\"navbar-split dropdown\" :class=\"{ active: topMenu === 'home' }\">\n                        <app-link class=\"dropdown-toggle\" aria-haspopup=\"true\" data-toggle=\"dropdown\" data-hover=\"dropdown\"><span>Shows</span>\n                            <b class=\"caret\" />\n                        </app-link>\n                        <ul class=\"dropdown-menu\">\n                            <li><app-link href=\"home/\"><i class=\"menu-icon-home\" />&nbsp;Show List</app-link></li>\n                            <li><app-link href=\"addShows/\"><i class=\"menu-icon-addshow\" />&nbsp;Add Shows</app-link></li>\n                            <li><app-link href=\"addRecommended/\"><i class=\"menu-icon-addshow\" />&nbsp;Add Recommended Shows</app-link></li>\n                            <li><app-link href=\"home/postprocess/\"><i class=\"menu-icon-postprocess\" />&nbsp;Manual Post-Processing</app-link></li>\n                            <li v-if=\"recentShows.length > 0\" role=\"separator\" class=\"divider\" />\n                            <li v-for=\"recentShow in recentShows\" :key=\"recentShow.link\">\n                                <app-link :href=\"recentShow.link\">\n                                    <i class=\"menu-icon-addshow\" />&nbsp;{{ recentShow.name }}\n                                </app-link>\n                            </li>\n                        </ul>\n                        <div style=\"clear:both;\" />\n                    </li>\n                    <li id=\"NAVschedule\" class=\"navbar-split\" :class=\"{ active: topMenu === 'schedule' }\">\n                        <app-link href=\"schedule/\">Schedule</app-link>\n                    </li>\n                    <li id=\"NAVhistory\" class=\"navbar-split\" :class=\"{ active: topMenu === 'history' }\">\n                        <app-link href=\"history/\">History</app-link>\n                    </li>\n                    <li id=\"NAVmanage\" class=\"navbar-split dropdown\" :class=\"{ active: topMenu === 'manage' }\">\n                        <app-link class=\"dropdown-toggle\" aria-haspopup=\"true\" data-toggle=\"dropdown\" data-hover=\"dropdown\">\n                            <span>Manage</span> <b class=\"caret\" />\n                        </app-link>\n                        <ul class=\"dropdown-menu\">\n                            <li><app-link href=\"manage/\"><i class=\"menu-icon-manage\" />&nbsp;Mass Update</app-link></li>\n                            <li><app-link href=\"manage/backlogOverview/\"><i class=\"menu-icon-backlog-view\" />&nbsp;Backlog Overview</app-link></li>\n                            <li><app-link href=\"manage/manageSearches/\"><i class=\"menu-icon-manage-searches\" />&nbsp;Manage Searches</app-link></li>\n                            <li><app-link href=\"manage/episodeStatuses/\"><i class=\"menu-icon-manage2\" />&nbsp;Episode Status Management</app-link></li>\n                            <li v-if=\"linkVisible.plex\"><a href=\"home/updatePLEX/\" @click.prevent=\"updatePlex\"><i class=\"menu-icon-plex\" />&nbsp;Update PLEX</a></li>\n                            <li v-if=\"linkVisible.kodi\"><a href=\"home/updateKODI/\" @click.prevent=\"updateKodi\"><i class=\"menu-icon-kodi\" />&nbsp;Update KODI</a></li>\n                            <li v-if=\"linkVisible.emby\"><a href=\"home/updateEMBY/\" @click.prevent=\"updateEmby\"><i class=\"menu-icon-emby\" />&nbsp;Update Emby</a></li>\n                            <!-- Avoid mixed content blocking by open manage torrent in new tab -->\n                            <li v-if=\"linkVisible.manageTorrents\"><app-link href=\"manage/manageTorrents/\" target=\"_blank\"><i class=\"menu-icon-bittorrent\" />&nbsp;Manage Torrents</app-link></li>\n                            <li v-if=\"linkVisible.failedDownloads\"><app-link href=\"manage/failedDownloads/\"><i class=\"menu-icon-failed-download\" />&nbsp;Failed Downloads</app-link></li>\n                            <li v-if=\"linkVisible.subtitleMissed\"><app-link href=\"manage/subtitleMissed/\"><i class=\"menu-icon-backlog\" />&nbsp;Missed Subtitle Management</app-link></li>\n                        </ul>\n                        <div style=\"clear:both;\" />\n                    </li>\n                    <li id=\"NAVconfig\" class=\"navbar-split dropdown\" :class=\"{ active: topMenu === 'config' }\">\n                        <app-link class=\"dropdown-toggle\" aria-haspopup=\"true\" data-toggle=\"dropdown\" data-hover=\"dropdown\">\n                            <span class=\"visible-xs-inline\">Config</span><img src=\"images/menu/system18.png\" class=\"navbaricon hidden-xs\"> <b class=\"caret\" />\n                        </app-link>\n                        <ul class=\"dropdown-menu\">\n                            <li><app-link href=\"config/\"><i class=\"menu-icon-help\" />&nbsp;Help &amp; Info</app-link></li>\n                            <li><app-link href=\"config/general/\"><i class=\"menu-icon-config\" />&nbsp;General</app-link></li>\n                            <li><app-link href=\"config/backuprestore/\"><i class=\"menu-icon-backup\" />&nbsp;Backup &amp; Restore</app-link></li>\n                            <li><app-link href=\"config/search/\"><i class=\"menu-icon-manage-searches\" />&nbsp;Search Settings</app-link></li>\n                            <li><app-link href=\"config/providers/\"><i class=\"menu-icon-provider\" />&nbsp;Search Providers</app-link></li>\n                            <li><app-link href=\"config/subtitles/\"><i class=\"menu-icon-backlog\" />&nbsp;Subtitles Settings</app-link></li>\n                            <li><app-link href=\"config/postProcessing/\"><i class=\"menu-icon-postprocess\" />&nbsp;Post-Processing</app-link></li>\n                            <li><app-link href=\"config/notifications/\"><i class=\"menu-icon-notification\" />&nbsp;Notifications</app-link></li>\n                            <li><app-link href=\"config/anime/\"><i class=\"menu-icon-anime\" />&nbsp;Anime</app-link></li>\n                        </ul>\n                        <div style=\"clear:both;\" />\n                    </li>\n                    <li id=\"NAVsystem\" class=\"navbar-split dropdown\" :class=\"{ active: topMenu === 'system' }\">\n                        <app-link class=\"padding-right-15 dropdown-toggle\" aria-haspopup=\"true\" data-toggle=\"dropdown\" data-hover=\"dropdown\"><span class=\"visible-xs-inline\">Tools</span><img src=\"images/menu/system18-2.png\" class=\"navbaricon hidden-xs\">\n                            <span v-if=\"toolsBadgeCount > 0\" :class=\"`badge${toolsBadgeClass}`\">{{ toolsBadgeCount }}</span>\n                            <b class=\"caret\" />\n                        </app-link>\n                        <ul class=\"dropdown-menu\">\n                            <li><app-link href=\"news/\"><i class=\"menu-icon-news\" />&nbsp;News <span v-if=\"system.news.unread > 0\" class=\"badge\">{{ system.news.unread }}</span></app-link></li>\n                            <li><app-link href=\"IRC/\"><i class=\"menu-icon-irc\" />&nbsp;IRC</app-link></li>\n                            <li><app-link href=\"changes/\"><i class=\"menu-icon-changelog\" />&nbsp;Changelog</app-link></li>\n                            <li role=\"separator\" class=\"divider\" />\n                            <li v-if=\"config.logs.numErrors > 0\"><app-link href=\"errorlogs/\"><i class=\"menu-icon-error\" />&nbsp;View Errors <span class=\"badge btn-danger\">{{config.logs.numErrors}}</span></app-link></li>\n                            <li v-if=\"config.logs.numWarnings > 0\"><app-link :href=\"`errorlogs/?level=${warningLevel}`\"><i class=\"menu-icon-viewlog-errors\" />&nbsp;View Warnings <span class=\"badge btn-warning\">{{config.logs.numWarnings}}</span></app-link></li>\n                            <li><app-link href=\"errorlogs/viewlog/\"><i class=\"menu-icon-viewlog\" />&nbsp;View Log</app-link></li>\n                            <li role=\"separator\" class=\"divider\" />\n                            <li><app-link :href=\"'home/update'\" @click.native.prevent=\"checkForupdates($event)\"><i class=\"menu-icon-update\" />&nbsp;Check For Updates</app-link></li>\n                            <li><app-link :href=\"'home/restart'\"><i class=\"menu-icon-restart\" />&nbsp;Restart</app-link></li>\n                            <li><app-link :href=\"'home/shutdown'\" @click.prevent=\"$router.push({ name: 'shutdown' });\"><i class=\"menu-icon-shutdown\" />&nbsp;Shutdown</app-link></li>\n                            <li v-if=\"username\"><app-link href=\"logout\" @click.native.prevent=\"confirmDialog($event, 'logout')\"><i class=\"menu-icon-shutdown\" />&nbsp;Logout</app-link></li>\n                            <li role=\"separator\" class=\"divider\" />\n                            <li><app-link href=\"home/status/\"><i class=\"menu-icon-info\" />&nbsp;Server Status</app-link></li>\n                        </ul>\n                        <div style=\"clear:both;\" />\n                    </li>\n                </ul>\n            </div><!-- /.navbar-collapse -->\n        </div><!-- /.container-fluid -->\n    </nav>\n</template>\n<script>\nimport { api } from '../api';\nimport { mapState } from 'vuex';\nimport { AppLink } from './helpers';\n\nexport default {\n    name: 'app-header',\n    components: {\n        AppLink\n    },\n    computed: {\n        ...mapState({\n            config: state => state.config.general,\n            subtitles: state => state.config.subtitles,\n            clients: state => state.config.clients,\n            notifiers: state => state.config.notifiers,\n            postprocessing: state => state.config.postprocessing,\n            search: state => state.config.search,\n            system: state => state.config.system,\n            isAuthenticated: state => state.auth.isAuthenticated,\n            username: state => state.auth.user.username,\n            warningLevel: state => state.config.general.logs.loggingLevels.warning\n        }),\n        /**\n         * Moved into a computed, so it's easier to mock in Jest.\n         * @returns {Object} - Route name and query.\n         */\n        currentShowRoute() {\n            const { $route } = this;\n            return {\n                name: $route.name,\n                query: $route.query\n            };\n        },\n        recentShows() {\n            const { config, currentShowRoute } = this;\n            const { recentShows } = config;\n\n            const hideActiveShow = show => !(currentShowRoute.name === 'show' && show.showSlug === currentShowRoute.query.showslug);\n\n            return recentShows.filter(hideActiveShow)\n                .map(show => {\n                    const link = `home/displayShow?showslug=${show.showSlug}`;\n                    return { name: show.name, link };\n                });\n        },\n        topMenu() {\n            return this.$route.meta.topMenu;\n        },\n        toolsBadgeCount() {\n            const { config } = this;\n            const { system } = this;\n            const { logs } = config;\n            const { news } = system;\n            return logs.numErrors + logs.numWarnings + news.unread;\n        },\n        toolsBadgeClass() {\n            const { config } = this;\n            const { logs } = config;\n            if (logs.numErrors > 0) {\n                return ' btn-danger';\n            }\n            if (logs.numWarnings > 0) {\n                return ' btn-warning';\n            }\n            return '';\n        },\n        linkVisible() {\n            const { clients, notifiers, search, subtitles } = this;\n            const { general } = search;\n            const { kodi, plex, emby } = notifiers;\n\n            return {\n                plex: plex.server.enabled && plex.server.host.length !== 0,\n                kodi: kodi.enabled && kodi.host.length !== 0,\n                /* @TODO: Originally there was a check to make sure the API key\n                   was configured for Emby: ` app.EMBY_APIKEY != '' ` */\n                emby: emby.enabled && emby.host,\n                manageTorrents: clients.torrents.enabled && clients.torrents.method !== 'blackhole',\n                failedDownloads: general.failedDownloads.enabled,\n                subtitleMissed: subtitles.enabled\n            };\n        }\n    },\n    mounted() {\n        const { $el } = this;\n\n        // Auto close menus when clicking a RouterLink\n        $el.clickCloseMenus = event => {\n            const { target } = event;\n            if (target.matches('#main_nav a.router-link, #main_nav a.router-link *') && (target.ariaExpanded === 'true' || target.ariaExpanded === null)) {\n                const dropdown = target.closest('.dropdown');\n                if (dropdown) {\n                    dropdown.querySelector('.dropdown-toggle').setAttribute('aria-expanded', false);\n                    dropdown.querySelector('.dropdown-menu').style.display = 'none';\n                }\n                // Also collapse the main nav if it's open\n                $('#main_nav').collapse('hide');\n            }\n        };\n        $el.addEventListener('click', $el.clickCloseMenus, { passive: true });\n\n        // Hover Dropdown for Nav\n        $($el).on({\n            mouseenter(event) {\n                const $target = $(event.currentTarget);\n                $target.find('.dropdown-menu').stop(true, true).delay(200).fadeIn(500, () => {\n                    $target.find('.dropdown-toggle').attr('aria-expanded', 'true');\n                });\n            },\n            mouseleave(event) {\n                const $target = $(event.currentTarget);\n                $target.find('.dropdown-toggle').attr('aria-expanded', 'false');\n                $target.find('.dropdown-menu').stop(true, true).delay(200).fadeOut(500);\n            }\n        }, 'ul.nav li.dropdown');\n    },\n    destroyed() {\n        // Revert `mounted()`\n        const { $el } = this;\n\n        // Auto close menus when clicking a RouterLink\n        $el.removeEventListener('click', $el.clickCloseMenus);\n\n        // Hover Dropdown for Nav\n        $($el).off('mouseenter mouseleave', 'ul.nav li.dropdown');\n\n        // @TODO Replace this with a real touchscreen check\n        // hack alert: if we don't have a touchscreen, and we are already hovering the mouse, then click should link instead of toggle\n        if ((navigator.maxTouchPoints || 0) < 2) {\n            $($el).off('click', '.dropdown-toggle');\n        }\n    },\n    methods: {\n        confirmDialog(event, action) {\n            const options = {\n                confirmButton: 'Yes',\n                cancelButton: 'Cancel',\n                dialogClass: 'modal-dialog',\n                post: false,\n                button: $(event.currentTarget || event.target),\n\n                confirm($element) {\n                    window.location.href = $element[0].href;\n                }\n            };\n\n            if (action === 'newversion') {\n                options.title = 'New version';\n                options.text = 'New version available, update now?';\n            } else if (action === 'logout') {\n                options.title = 'Logout';\n                options.text = 'Are you sure you want to logout from Medusa?';\n            } else {\n                return;\n            }\n\n            $.confirm(options, event);\n        },\n        async checkForupdates(event) {\n            const { confirmDialog } = this;\n            try {\n                this.$snotify.info(\n                    'Checking for a new version...'\n                );\n                await api.post('system/operation', { type: 'CHECKFORUPDATE' });\n                confirmDialog(event, 'newversion');\n            } catch (error) {\n                this.$snotify.info(\n                    'You are already on the latest version'\n                );\n            }\n        },\n        async updateKodi() {\n            try {\n                await api.post('notifications/kodi/update');\n            } catch (error) {\n                this.$snotify.info(\n                    'Error trying to update kodi'\n                );\n            }\n        },\n        async updateEmby() {\n            try {\n                await api.post('notifications/emby/update');\n            } catch (error) {\n                this.$snotify.info(\n                    'Error trying to update emby'\n                );\n            }\n        },\n        async updatePlex() {\n            try {\n                await api.post('notifications/plex/update');\n            } catch (error) {\n                this.$snotify.info(\n                    'Error trying to update plex'\n                );\n            }\n        }\n    }\n};\n</script>\n\n<style>\n.floating-badge {\n    position: absolute;\n    top: -5px;\n    right: -8px;\n    padding: 0 4px;\n    background-color: #777;\n    border: 2px solid #959595;\n    border-radius: 100px;\n    font-size: 12px;\n    font-weight: bold;\n    text-decoration: none;\n    color: white;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
module.exports = ___CSS_LOADER_EXPORT___;
