            });\n                this.testResult = response.data;\n            } catch (error) {\n                this.testResult = error;\n                this.$snotify.error(\n                    `Error while trying to test provider ${providerName} for results`,\n                    'Error'\n                );\n            } finally {\n                this.loading = false;\n            }\n        }\n    }\n};\n</script>\n<style scoped>\n.test-provider {\n    float: left;\n}\n\n.test-provider > button {\n    margin: 0 5px;\n}\n\n.testresult {\n    border-style: solid;\n    border-width: 1px;\n    padding: 1px 4px 4px 4px;\n    border-color: #ccc;\n}\n</style>\n"],"sourceRoot":""}]);
