___CSS_LOADER_EXPORT___.push([module.id, "\nbutton[data-v-42ae8ba8] {\n    margin-bottom: 1em;\n}\n", "",{"version":3,"sources":["webpack://./src/components/log-reporter.vue"],"names":[],"mappings":";AAuHA;IACA,kBAAA;AACA","sourcesContent":["<template>\n    <div id=\"log-reporter\">\n        <div class=\"row\">\n            <div class=\"col-md-12\">\n                <!-- Toggle auto update -->\n                <div class=\"show-option\">\n                    <button @click=\"autoUpdate = !autoUpdate\" type=\"button\" class=\"btn-medusa btn-inline\">\n                        <i :class=\"`glyphicon glyphicon-${autoUpdate ? 'pause' : 'play'}`\" />\n                        {{ autoUpdate ? 'Pause' : 'Resume' }}\n                    </button>\n                </div>\n            </div>\n        </div>\n        <div class=\"row\">\n            <div class=\"col-md-12\">\n                <template v-if=\"logs.length > 0\">\n                    <pre><div v-for=\"(log, idx) in logs\" :key=\"idx\">{{log}}</div></pre>\n                </template>\n                <template v-else>\n                    <pre>There are no events to display</pre>\n                </template>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { api } from '../api';\nimport { mapState } from 'vuex';\n\nexport default {\n    name: 'log-reporter',\n    props: {\n        level: {\n            type: String,\n            default: '40' // Default level of ERROR\n        }\n    },\n    data() {\n        return {\n            logs: [],\n            autoUpdate: true,\n            autoUpdateTimer: null\n        };\n    },\n    mounted() {\n        if (this.autoUpdate) {\n            this.autoUpdateTask();\n        } else {\n            this.fetchLogs();\n        }\n    },\n    computed: {\n        ...mapState({\n            loggingLevels: state => state.config.general.logs.loggingLevels\n        }),\n        header() {\n            const { logLevel, loggingLevels } = this;\n            if (logLevel === loggingLevels.warning) {\n                return 'Warning Logs';\n            }\n            return 'Error Logs';\n        },\n        logLevel() {\n            return this.$route.query.level || this.level;\n        }\n    },\n    methods: {\n        async fetchLogs() {\n            const { logLevel, loggingLevels } = this;\n            if (Object.keys(this.loggingLevels).length === 0) {\n                return;\n            }\n\n            const logLevelString = Object.keys(loggingLevels).find(\n                key => loggingLevels[key] === Number(logLevel)\n            ).toUpperCase();\n\n            try {\n                const { data } = await api.get('log/reporter', { params: { level: logLevelString } });\n                this.logs = data;\n                return true;\n            } catch (error) {\n                this.$snotify.error(\n                    `Error while trying to get logs with level ${logLevel}`,\n                    'Error'\n                );\n                return false;\n            }\n        },\n        async autoUpdateTask(errors = 0) {\n            if (this.autoUpdate) {\n                const result = await this.fetchLogs();\n                // Increment if false\n                errors += Number(!result);\n                // Stop after 5 network errors\n                if (errors < 5) {\n                    this.autoUpdateTimer = setTimeout(this.autoUpdateTask, 1500, errors);\n                } else {\n                    this.autoUpdate = false;\n                    this.autoUpdateTimer = null;\n                }\n            } else {\n                this.autoUpdateTimer = null;\n            }\n        }\n    },\n    watch: {\n        logLevel() {\n            this.fetchLogs();\n        },\n        autoUpdate() {\n            this.autoUpdateTask();\n        }\n    }\n};\n</script>\n\n<style scoped>\nbutton {\n    margin-bottom: 1em;\n}\n</style>\n"],"sourceRoot":""}]);
