___CSS_LOADER_EXPORT___.push([module.id, "\n.warning-enter-active[data-v-16387a4c],\n.warning-leave-active[data-v-16387a4c] {\n    -moz-transition-duration: 0.3s;\n    -webkit-transition-duration: 0.3s;\n    -o-transition-duration: 0.3s;\n    transition-duration: 0.3s;\n    -moz-transition-timing-function: ease-in;\n    -webkit-transition-timing-function: ease-in;\n    -o-transition-timing-function: ease-in;\n    transition-timing-function: ease-in;\n}\n.warning-enter-to[data-v-16387a4c],\n.warning-leave[data-v-16387a4c] {\n    max-height: 100%;\n}\n.warning-enter[data-v-16387a4c],\n.warning-leave-to[data-v-16387a4c] {\n    max-height: 0;\n}\n.warning[data-v-16387a4c] {\n    display: block;\n    overflow: hidden;\n    width: 100%;\n    position: absolute;\n    left: 0;\n    background-color: #e23636;\n    padding: 0 2px 0 2px;\n    z-index: 1;\n}\n.manager-note[data-v-16387a4c] {\n    margin: 5px 0;\n    padding: 10px;\n    border: 1px solid #ccc;\n}\n.manager-note > img[data-v-16387a4c] {\n    width: 16px;\n    padding-bottom: 4px;\n}\n", "",{"version":3,"sources":["webpack://./src/components/helpers/config-custom-torznab.vue"],"names":[],"mappings":";AAsOA;;IAEA,8BAAA;IACA,iCAAA;IACA,4BAAA;IACA,yBAAA;IACA,wCAAA;IACA,2CAAA;IACA,sCAAA;IACA,mCAAA;AACA;AAEA;;IAEA,gBAAA;AACA;AAEA;;IAEA,aAAA;AACA;AAEA;IACA,cAAA;IACA,gBAAA;IACA,WAAA;IACA,kBAAA;IACA,OAAA;IACA,yBAAA;IACA,oBAAA;IACA,UAAA;AACA;AAEA;IACA,aAAA;IACA,aAAA;IACA,sBAAA;AACA;AAEA;IACA,WAAA;IACA,mBAAA;AACA","sourcesContent":["<template>\n    <div id=\"custom-torznab\">\n        <config-template label-for=\"select_torznab_provider\" label=\"Select Provider\">\n            <select id=\"select-provider\" class=\"form-control input-sm\" v-model=\"selectedProvider\">\n                <option value=\"#add\">--- add new provider ---</option>\n                <option :value=\"option.value\" v-for=\"option in torznabProviderOptions\" :key=\"option.value\">\n                    {{ option.text }}\n                </option>\n            </select>\n        </config-template>\n\n        <!-- Edit Provider -->\n        <div v-if=\"currentProvider && selectedProvider !== '#add'\" class=\"edit-provider\">\n            <config-textbox disabled v-model=\"currentProvider.name\" label=\"Provider name\" id=\"edit_provider_name\" />\n            <config-textbox disabled v-model=\"currentProvider.url\" label=\"Site Url\" id=\"edit_provider_url\" />\n            <config-textbox type=\"password\" v-model=\"currentProvider.config.apikey\" label=\"Api key\" id=\"edit_provider_api\" />\n\n            <config-template label=\"Categories\" label-for=\"catids\">\n                <multiselect\n                    :value=\"providerCatIds\"\n                    :multiple=\"true\"\n                    :options=\"availableCategories\"\n                    label=\"id\"\n                    track-by=\"id\"\n                    :taggable=\"true\"\n                    tag-placeholder=\"Add this as new cat id\" placeholder=\"Search or add a cat id\"\n                    @tag=\"addTag\"\n                    @input=\"currentProvider.config.catIds = $event.map(cat => cat.id)\"\n                >\n                    <template slot=\"option\" slot-scope=\"props\">\n                        <span v-if=\"props.option.isTag\"><strong>{{props.option.label}}</strong></span>\n                        <span v-else><strong>{{props.option.id}}</strong> ({{props.option.name}})</span>\n                    </template>\n                </multiselect>\n            </config-template>\n\n            <button :disabled=\"currentProvider.default\" class=\"btn-medusa btn-danger torznab_delete\" id=\"torznab_delete\" @click=\"removeProvider\">Delete</button>\n            <button class=\"btn-medusa config_submitter_refresh\" @click=\"$emit('save')\">Save Changes</button>\n            <p class=\"manager-note\" v-if=\"currentProvider.manager === 'prowlarr'\">\n                <img src=\"images/providers/prowlarr.png\" style=\"width: 16px\">\n                Note! This is a provider configured through the 'Configure Custom Prowlarr Providers' tab.\n            </p>\n        </div>\n\n        <!-- Add Provider -->\n        <div v-if=\"selectedProvider === '#add'\" class=\"add-provider\">\n            <config-textbox v-model=\"name\" label=\"Provider name\" id=\"add_provider_name\">\n                <template v-slot:warning>\n                    <transition name=\"warning\">\n                        <div v-if=\"!providerIdAvailable\" class=\"warning\">This provider id is already used.</div>\n                    </transition>\n                </template>\n            </config-textbox>\n            <config-textbox v-model=\"url\" label=\"Site Url\" id=\"add_provider_url\" />\n            <config-textbox type=\"password\" v-model=\"apikey\" label=\"Api key\" id=\"add_provider_api\" />\n\n            <button :disabled=\"!providerIdAvailable\" class=\"btn-medusa config_submitter\" @click=\"addProvider\">Add Provider</button>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { api } from '../../api';\nimport { mapGetters, mapState } from 'vuex';\nimport { ADD_PROVIDER, REMOVE_PROVIDER } from '../../store/mutation-types';\nimport {\n    ConfigTextbox,\n    ConfigTemplate\n} from '.';\nimport Multiselect from 'vue-multiselect';\n\nexport default {\n    name: 'config-custom-torznab',\n    components: {\n        ConfigTextbox,\n        ConfigTemplate,\n        Multiselect\n    },\n    data() {\n        return {\n            saving: false,\n            selectedProvider: '#add',\n            name: '',\n            url: '',\n            apikey: '',\n            availableCategories: []\n        };\n    },\n    methods: {\n        async save() {\n            const { provider } = this;\n            // Disable the save button until we're done.\n            this.saving = true;\n\n            try {\n                await api.patch(`providers/${provider.id}`, provider.config);\n                this.$snotify.success(\n                    `Saved provider ${provider.name}`,\n                    'Saved',\n                    { timeout: 5000 }\n                );\n            } catch (error) {\n                this.$snotify.error(\n                    `Error while trying to save provider ${provider.name}`,\n                    'Error'\n                );\n            } finally {\n                this.saving = false;\n            }\n        },\n        async getCategories() {\n            const { currentProvider } = this;\n            if (!currentProvider.name || !currentProvider.url || !currentProvider.config.apikey) {\n                return;\n            }\n\n            try {\n                const response = await api.post('providers/torznab/operation', {\n                    type: 'GETCATEGORIES',\n                    apikey: currentProvider.config.apikey,\n                    name: currentProvider.name,\n                    url: currentProvider.url\n                });\n                if (response.data.result.success) {\n                    this.availableCategories = response.data.result.categories;\n                }\n            } catch (error) {\n                this.$snotify.error(\n                    `Error while trying to get cats for provider ${currentProvider.name}`,\n                    'Error'\n                );\n            }\n        },\n        async addProvider() {\n            const { name, apikey, url } = this;\n            try {\n                const response = await api.post('providers/torznab', { apikey, name, url });\n                this.$store.commit(ADD_PROVIDER, response.data.result);\n                this.$snotify.success(\n                    `Saved provider ${name}`,\n                    'Saved',\n                    { timeout: 5000 }\n                );\n                this.apikey = '';\n                this.name = '';\n                this.url = '';\n            } catch (error) {\n                this.$snotify.error(\n                    `Error while trying to get cats for provider ${name}`,\n                    'Error'\n                );\n            }\n        },\n        async removeProvider() {\n            const { currentProvider } = this;\n            try {\n                await api.delete(`providers/torznab/${currentProvider.id}`);\n                this.$store.commit(REMOVE_PROVIDER, currentProvider.id);\n                this.$snotify.success(\n                    `Removed provider ${currentProvider.name}`,\n                    'Removed',\n                    { timeout: 5000 }\n                );\n                this.selectedProvider = '#add';\n            } catch (error) {\n                this.$snotify.error(\n                    `Error while trying to remove provider ${currentProvider.name}`,\n                    'Error'\n                );\n            }\n        },\n        /**\n         * Add custom cat id's.\n         * @param {string} newTag category id.\n         */\n        addTag(newTag) {\n            if (!Number(newTag) || Number(newTag) < 1) {\n                return;\n            }\n            const tag = { id: newTag, name: newTag };\n            this.availableCategories.push(tag);\n        }\n    },\n    computed: {\n        ...mapState({\n            providers: state => state.provider.providers\n        }),\n        ...mapGetters(['providerNameToId']),\n        torznabProviderOptions() {\n            const { providers } = this;\n            return providers.filter(prov => prov.subType === 'torznab').map(prov => {\n                return ({ value: prov.id, text: prov.name });\n            });\n        },\n        currentProvider() {\n            const { providers, selectedProvider } = this;\n            if (!selectedProvider) {\n                return null;\n            }\n            return providers.find(prov => prov.id === selectedProvider);\n        },\n        providerCatIds() {\n            const { currentProvider } = this;\n            if (!currentProvider || currentProvider.config.catIds.length === 0) {\n                return [];\n            }\n\n            // Check if we have a list of objects.\n            if (currentProvider.config.catIds.every(x => typeof x === 'string')) {\n                return currentProvider.config.catIds.map(cat => ({ id: cat, name: null }));\n            }\n\n            return currentProvider.config.catIds;\n        },\n        providerIdAvailable() {\n            const { providerNameToId, providers, name } = this;\n            return providers.filter(provider => providerNameToId(name) === provider.id).length === 0;\n        }\n    },\n    watch: {\n        currentProvider(newProvider, oldProvider) {\n            if (newProvider && newProvider !== oldProvider) {\n                this.getCategories();\n            }\n        }\n    }\n};\n</script>\n\n<style scoped>\n.warning-enter-active,\n.warning-leave-active {\n    -moz-transition-duration: 0.3s;\n    -webkit-transition-duration: 0.3s;\n    -o-transition-duration: 0.3s;\n    transition-duration: 0.3s;\n    -moz-transition-timing-function: ease-in;\n    -webkit-transition-timing-function: ease-in;\n    -o-transition-timing-function: ease-in;\n    transition-timing-function: ease-in;\n}\n\n.warning-enter-to,\n.warning-leave {\n    max-height: 100%;\n}\n\n.warning-enter,\n.warning-leave-to {\n    max-height: 0;\n}\n\n.warning {\n    display: block;\n    overflow: hidden;\n    width: 100%;\n    position: absolute;\n    left: 0;\n    background-color: #e23636;\n    padding: 0 2px 0 2px;\n    z-index: 1;\n}\n\n.manager-note {\n    margin: 5px 0;\n    padding: 10px;\n    border: 1px solid #ccc;\n}\n\n.manager-note > img {\n    width: 16px;\n    padding-bottom: 4px;\n}\n</style>\n"],"sourceRoot":""}]);
