asset/${type}?api_key=${apiKey}`;\n        },\n        href() {\n            const { link, src } = this;\n            // Compute a link to the full asset, if applicable\n            if (link) {\n                return src.replace('Thumb', '');\n            }\n            return undefined;\n        },\n        newCls() {\n            const { cls, imgWidth } = this;\n            let newClass = cls;\n\n            if (imgWidth) {\n                newClass += ` width-${imgWidth}`;\n            }\n\n            return newClass;\n        },\n        newStyle() {\n            const { imgWidth, imgStyle } = this;\n            let tempStyle = '';\n            if (imgStyle) {\n                tempStyle = imgStyle;\n            }\n\n            if (imgWidth) {\n                tempStyle += `;width=${imgWidth}px`;\n            }\n            return tempStyle;\n        }\n    }\n};\n</script>\n<style scoped>\n.width-40 {\n    width: 40px;\n}\n\n.width-50 {\n    width: 50px;\n}\n\n</style>\n"],"sourceRoot":""}]);
