CA,0BAAA;IACA,4BAAA;AACA;AAEA;;IAEA,gBAAA;IACA,uBAAA;IACA,cAAA;IACA,eAAA;IACA,mBAAA;AACA","sourcesContent":["<template>\n    <div class=\"calendar-wrapper\" :class=\"{fanartOpacity: layout.fanartBackgroundOpacity}\">\n        <div class=\"wrap-center\">\n            <div class=\"day-column\" v-for=\"day in groupedSchedule\" :key=\"day.airdate\">\n                <span class=\"day-header\" :title=\"day.airdate ? `airs ${day.airdate}` : 'No shows for this day'\">{{day.header}}</span>\n                <ul v-if=\"day.episodes.length > 0\">\n                    <li v-for=\"episode in day.episodes\" :key=\"`${episode.showSlug}${episode.episodeSlug}`\" class=\"calendar-show\">\n                        <div class=\"poster\">\n                            <app-link :title=\"episode.showName\" :href=\"`home/displayShow?showslug=${episode.showSlug}`\">\n                                <asset default-src=\"images/poster.png\" :show-slug=\"episode.showSlug\" type=\"posterThumb\" :link=\"false\" />\n                            </app-link>\n                        </div>\n                        <div class=\"text\">\n                            <span class=\"airtime\">\n                                {{episode.airsTime}} on {{episode.network}}\n                            </span>\n                            <span class=\"episode-title\" :title=\"episode.epName\">\n                                {{episode.episodeSlug}} - {{episode.epName}}\n                            </span>\n                        </div>\n                    </li>\n                </ul>\n                <ul v-else>\n                    <li class=\"calendar-show\">\n                        <div class=\"text\">\n                            <span class=\"airtime\">No shows for this day</span>\n                        </div>\n                    </li>\n                </ul>\n            </div>\n        </div>\n        <backstretch :slug=\"general.randomShowSlug\" />\n    </div>\n</template>\n<script>\nimport { mapGetters, mapState } from 'vuex';\nimport { AppLink, Asset } from '../helpers';\nimport Backstretch from '../backstretch.vue';\n\nexport default {\n    name: 'calendar',\n    components: {\n        AppLink,\n        Asset,\n        Backstretch\n    },\n    computed: {\n        ...mapState({\n            layout: state => state.config.layout,\n            general: state => state.config.general\n        }),\n        ...mapGetters([\n            'groupedSchedule'\n        ])\n    }\n};\n</script>\n\n<style scoped>\n.shows-today {\n    background-color: rgb(245, 241, 228);\n}\n\n.shows-soon {\n    background-color: rgb(221, 255, 221);\n}\n\n.shows-missed {\n    background-color: rgb(255, 221, 221);\n}\n\n.shows-later {\n    background-color: rgb(190, 222, 237);\n}\n\nul {\n    margin: 0;\n    padding: 0;\n}\n\nul > li {\n    list-style: none;\n}\n\n.calendar-wrapper {\n    display: flex;\n    justify-content: center;\n}\n\n.wrap-center {\n    display: flex;\n    flex-direction: row;\n    flex-wrap: wrap;\n\n    --column-width: 15rem;\n    --poster-height: 22rem;\n}\n\n.day-column {\n    display: block;\n    width: var(--column-width);\n    color: rgb(0, 0, 0);\n    margin-bottom: 2rem;\n}\n\n.calendar-show .text {\n    padding: 3px 5px 10px;\n    width: var(--column-width);\n}\n\n.day-header {\n    display: block;\n    text-align: center;\n    border-collapse: collapse;\n    font-weight: normal;\n    padding: 4px;\n    height: 3rem;\n}\n\n.calendar-show .poster >>> img {\n    width: var(--column-width);\n    height: var(--poster-height);\n}\n\n.calendar-show .text .airtime,\n.calendar-show .text .episode-title {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: block;\n    font-size: 11px;\n    white-space: nowrap;\n}\n</style>\n"],"sourceRoot":""}]);
