___CSS_LOADER_EXPORT___.push([module.id, "\n.shows-today[data-v-fcea503e] {\n    background-color: rgb(245, 241, 228);\n}\n.shows-soon[data-v-fcea503e] {\n    background-color: rgb(221, 255, 221);\n}\n.shows-missed[data-v-fcea503e] {\n    background-color: rgb(255, 221, 221);\n}\n.shows-later[data-v-fcea503e] {\n    background-color: rgb(190, 222, 237);\n}\n", "",{"version":3,"sources":["webpack://./src/components/schedule/banner.vue"],"names":[],"mappings":";AAiDA;IACA,oCAAA;AACA;AAEA;IACA,oCAAA;AACA;AAEA;IACA,oCAAA;AACA;AAEA;IACA,oCAAA;AACA","sourcesContent":["<template>\n    <div class=\"banner-wrapper\">\n        <template v-if=\"sort === 'show'\">\n            <banner-card v-for=\"episode in filteredSchedule.sort((a, b) => a.showName.localeCompare(b.showName))\"\n                         :key=\"`${episode.showSlug}${episode.episodeSlug}`\" :episode=\"episode\"\n                         :class=\"`shows-${episode.class}`\"\n            />\n        </template>\n        <!-- For the sort by date and network options -->\n        <template v-else>\n            <div  v-for=\"group in sortedSchedule(sort)\" :key=\"group.header\">\n                <!-- Coming shows are grouped per week day -->\n                <h2 class=\"day\">{{group.header}}</h2>\n                <banner-card v-for=\"episode in group.episodes\"\n                             :key=\"`${episode.showSlug}${episode.episodeSlug}`\" :episode=\"episode\"\n                             :class=\"`shows-${episode.class}`\"\n                />\n            </div>\n        </template>\n    </div>\n</template>\n<script>\nimport { mapGetters, mapState } from 'vuex';\nimport BannerCard from './banner-card.vue';\n\nexport default {\n    name: 'banner',\n    components: {\n        BannerCard\n    },\n    computed: {\n        ...mapState({\n            layout: state => state.config.layout,\n            displayPaused: state => state.config.layout.comingEps.displayPaused,\n            sort: state => state.config.layout.comingEps.sort\n        }),\n        ...mapGetters([\n            'getScheduleFlattened',\n            'sortedSchedule'\n        ]),\n        filteredSchedule() {\n            const { displayPaused, getScheduleFlattened } = this;\n            return getScheduleFlattened.filter(item => !item.paused || displayPaused);\n        }\n    }\n};\n</script>\n\n<style scoped>\n.shows-today {\n    background-color: rgb(245, 241, 228);\n}\n\n.shows-soon {\n    background-color: rgb(221, 255, 221);\n}\n\n.shows-missed {\n    background-color: rgb(255, 221, 221);\n}\n\n.shows-later {\n    background-color: rgb(190, 222, 237);\n}\n</style>\n"],"sourceRoot":""}]);
